					<div style="background-color: rgb(238, 238, 238)" class="weui-cells__title">申请流程</div>
					<div class="step_box">
            <div class="step line"><img src="@/assets/true.png" alt=""><span>提交</span></div>
            <div class="step line"><img src="@/assets/true.png" alt=""><span>审批中</span></div>
            <div class="step"><img src="@/assets/true.png" alt=""><span>审批完成</span></div>
          </div>
								
					<div style="background-color: rgb(238, 238, 238)" class="weui-cells__title">外出信息</div>
					<div class="weui-cells weui-cells_form">
				
						<div class="info_list">
							<div><span class="bt">*</span>开始日期</div>
							<div class="form_select">
								<div class="dOutStartDate">{{date}}</div>
								<span id="dOutStartDate">选择</span>
							</div>
						</div>
						
						<div class="info_list">
							<div><span class="bt">*</span>结束日期</div>
							<div class="form_select">
								<div class="dOutEndDate">{{date}}</div>
								<span id="dOutEndDate">选择</span>
							</div>
						</div>
						
						<div class="info_list">
							<div><span class="bt">*</span>外出地点</div>
							<input type="text" name="sOutAddress" value="番禺区" placeholder="外出地点(请具体填写到区/县)" autocomplete="off"/>
						</div>
						
						<div class="weui-input-row" style="padding: 0 .3rem .3rem .3rem;">
							<label class="smTitle"><span class="bt">*</span><span class="sNowAddr_required">外出事由</span> </label>
							<div class="weui-cell" style="padding: 0rem;">
								<div class="weui-cell__bd">
									<textarea class="weui-textarea" name="sOutReason" placeholder="" rows="3"  style="border: 1px solid #efefef;">上英语课</textarea>
								</div>
							</div>
						</div>
						
					</div>
					
					<div style="background-color: rgb(238, 238, 238)" class="weui-cells__title">我的信息</div>
            <div class="info_box">
              <div class="info_list" >
                <span>姓名</span>
                <span>王玲</span>
              </div>
              
              <div class="info_list">
                <span>学号</span>
                <span>202130420370</span>
              </div>

              <div class="info_list">
                <span>学院</span>
                <span>外国语学院</span>
              </div>

              <div class="info_list">
                <span>年级</span>
                <span>2021</span>	
              </div>

              <div class="info_list">
                <span>人员类型</span>
                <span>本科生</span>
              </div>
            </div>
						
			
					<div class="info_box"></div>

					<div style="background-color: rgb(238, 238, 238)" class="bottomTips">温馨提示：每次只能发起一次外出申请，外出申请生效期间内无法再次发起申请。</div>
