			</form>
		</div>
    <div class="logo_box" style="display:block">
      
      <img id="logo" src="@/assets/pass.png" alt="">
    </div>
  </div>
</template>

<script>
import moment from 'moment'; 
export default {
  
  name: 'App',
  data(){
    return{
      date:"1900-01-01"
    }
  },
  methods:{

  },
  created(){
    // console.log(this.date)
    this.date = moment().format('YYYY-MM-DD')
  }
}
</script>

<style>
@import "assets/layui/css/layui.css";
@import "assets/weui/css/weui.css";
@import "assets/apply.css";
.right{
  float: right;
}

.step_box {
    display: flex;
    justify-content: center;
    color: #7f7f7f;
    padding: .3rem 0;
    background-color: #fff;
}

.logo_box {
    display: none;
    position: fixed;
    width: 5.5rem;
    top: 2rem;
    left: calc((100% - 5.5rem) / 2);
}

.step.line::after {
    content: "——";
    margin: .1rem;
}
</style>



